import './App.css';
import {BrowserRouter, Switch, Route} from 'react-router-dom'
import { NavBar } from './components/NavBar';
import { LoginContainer } from './components/Login/LoginContainer/LoginContainer';
import { RegisterContainer } from './components/Login/Register/RegisterContainer';
import { ProductContainer } from './components/Main/ProductContainer/ProductContainer';


function App() {
  return (
    <div className="App container">
      <NavBar />
        <BrowserRouter>
          <Switch>
            <Route exact path="/login">
              <LoginContainer />
            </Route>  
            <Route exact path="/register">
              <RegisterContainer />
            </Route>  
            <Route path="/" render={(props)=><ProductContainer {...props} /> }/>
          </Switch>
        </BrowserRouter>

    </div>
  );
}

export default App;


